import { ChainId } from '@oolongswap/oolongswap-sdk'
import React from 'react'
import { NavLink } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

import styled from 'styled-components'

import { useActiveWeb3React } from '../../hooks'
import { useTokenBalance } from '../../state/wallet/hooks'
import { OOLONG } from '../../constants'
// import { useETHBalances } from '../../state/wallet/hooks'

import { YellowCard } from '../Card'
import Menu from '../Menu'

import Row from '../Row'
import Web3Status from '../Web3Status'
import { CoinIcon, Logo } from 'assets/imgs'

const HeaderFrame = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  top: 0;
  position: relative;
  padding: 0 72px;
  height: 80px;
  z-index: 2;
  @media screen and (max-width: 1024px) {
    padding: 0 16px;
  }
`

const HeaderControls = styled.div`
  display: grid;
  gap: 10px;
  align-items: center;
  grid-auto-flow: column;
`

const HeaderLinks = styled(Row)`
  display: grid;
  width: fit-content;
  gap: 34px;
  align-items: center;
  grid-auto-flow: column;
  @media screen and (max-width: 1024px) {
    height: 80px;
    position: fixed;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    align-items: center;
  }
`

const NetworkCard = styled(YellowCard)`
  border-radius: 8px;
  padding: 0 12px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  height: 44px;
  display: flex;
  align-items: center;
  @media screen and (max-width: 560px) {
    display: none;
  }
`

const Title = styled.a`
  display: grid;
  gap: 10px;
  align-items: center;
  grid-auto-flow: column;
  cursor: pointer;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: ${({ theme }) => theme.primary};
  @media screen and (max-width: 560px) {
    span {
      display: none;
    }
  }
`

const activeClassName = 'ACTIVE'

const StyledNavLink = styled(NavLink).attrs({
  activeClassName
})`
  cursor: pointer;
  color: ${({ theme }) => theme.font85};
  font-size: 1rem;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  &.${activeClassName} {
    color: ${({ theme }) => theme.bgc1};
    ::after {
      content: '';
      width: 100%;
      height: 2px;
      background: ${({ theme }) => theme.bgc1};
      position: absolute;
      bottom: -8px;
      left: 0;
    }
  }

  :hover,
  :focus {
    color: ${({ theme }) => theme.bgc1};
  }
`

export const StyledMenuButton = styled.button`
  position: relative;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  height: 35px;
  background-color: ${({ theme }) => theme.bgc3};
  margin-left: 8px;
  padding: 0.15rem 0.5rem;
  border-radius: 0.5rem;

  :hover,
  :focus {
    cursor: pointer;
    outline: none;
    background-color: ${({ theme }) => theme.bgc4};
  }

  svg {
    margin-top: 2px;
  }
  > * {
    stroke: ${({ theme }) => theme.text1};
  }
`

const CoinWrapper = styled.div`
  display: grid;
  gap: 4px;
  grid-auto-flow: column;
  align-items: center;
  span {
    color: ${({ theme }) => theme.font85};
    font-weight: 600;
    font-size: 16px;
  }
  @media screen and (max-width: 560px) {
    display: none;
  }
`
const CoinLogo = styled.div`
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: ${({ theme }) => theme.bgc2};
  display: flex;
  align-items: center;
  justify-content: center;
`

const NETWORK_LABELS: { [chainId in ChainId]?: string } = {
  [ChainId.RINKEBY]: 'Rinkeby',
  [ChainId.BOBA_RINKEBY]: 'Boba Rinkeby'
}

export default function Header() {
  const { account, chainId } = useActiveWeb3React()
  const { t } = useTranslation()
  const userOolongBalance = useTokenBalance(account? account : undefined, chainId? OOLONG[chainId] : undefined)

  // const userEthBalance = useETHBalances(account ? [account] : [])?.[account ?? '']
  // // const [isDark] = useDarkModeManager()
  // const [darkMode, toggleDarkMode] = useDarkModeManager()

  return (
    <HeaderFrame>
      <Title href=".">
        <Logo />
        <span>Oolongswap</span>
      </Title>
      <HeaderLinks>
        <StyledNavLink id={`swap-nav-link`} to={'/swap'}>
          {t('swap')}
        </StyledNavLink>
        <StyledNavLink
          id={`pool-nav-link`}
          to={'/pool'}
          isActive={(match, { pathname }) =>
            Boolean(match) ||
            pathname.startsWith('/add') ||
            pathname.startsWith('/remove') ||
            pathname.startsWith('/find')
          }
        >
          {t('pool')}
        </StyledNavLink>
        <StyledNavLink id={`stake-nav-link`} to={'/farm'}>
          Farm
        </StyledNavLink>
        {/* <StyledExternalLink id={`stake-nav-link`} href={'https://info.oolongswap.com'}>
          Charts <span style={{ fontSize: '11px' }}>↗</span>
        </StyledExternalLink> */}
      </HeaderLinks>
      <HeaderControls>
        <CoinWrapper>
          <CoinLogo>
            <CoinIcon />
          </CoinLogo>
          <span>{userOolongBalance?.toSignificant(2)}</span>
        </CoinWrapper>
        {chainId && NETWORK_LABELS[chainId] && (
          <NetworkCard title={NETWORK_LABELS[chainId]}>{NETWORK_LABELS[chainId]}</NetworkCard>
        )}
        {/* <AccountElement active={!!account} style={{ pointerEvents: 'auto' }}>
            {account && userEthBalance ? (
              <BalanceText style={{ flexShrink: 0 }} pl="0.75rem" pr="0.5rem" fontWeight={500}>
                {userEthBalance?.toSignificant(4)} ETH
              </BalanceText>
            ) : null}
          </AccountElement> */}
        <Web3Status />
        {/* <StyledMenuButton onClick={() => toggleDarkMode()}>
            {darkMode ? <Moon size={20} /> : <Sun size={20} />}
          </StyledMenuButton> */}
        <Menu />
      </HeaderControls>
    </HeaderFrame>
  )
}
