function renderTransactions(transactions: string[]) {
  return transactions.map((hash, i) => {
    return <Transaction key={i} hash={hash} />
  })
}

interface AccountDetailsProps {
  toggleWalletModal: () => void
  pendingTransactions: string[]
  confirmedTransactions: string[]
  ENSName?: string
  openOptions: () => void
}

export default function AccountDetails({
  toggleWalletModal,
  pendingTransactions,
  confirmedTransactions,
  ENSName,
  openOptions
}: AccountDetailsProps) {
  const { chainId, account, connector } = useActiveWeb3React()
  const dispatch = useDispatch<AppDispatch>()

  function formatConnectorName() {
    const { ethereum } = window
    const isMetaMask = !!(ethereum && ethereum.isMetaMask)
    const name = Object.keys(SUPPORTED_WALLETS)
      .filter(
        k =>
          SUPPORTED_WALLETS[k].connector === connector && (connector !== injected || isMetaMask === (k === 'METAMASK'))
      )
      .map(k => SUPPORTED_WALLETS[k].name)[0]
    return <WalletName>Connected with {name}</WalletName>
  }

  function getStatusIcon() {
    if (connector === injected) {
      return <Identicon />
    } else if (connector === walletconnect) {
      return <img src={WalletConnectIcon} alt={'wallet connect logo'} />
    } else if (connector === walletlink) {
      return <img src={CoinbaseWalletIcon} alt={'coinbase wallet logo'} />
    } else if (connector === fortmatic) {
      return <img src={FortmaticIcon} alt={'fortmatic logo'} />
    } else if (connector === portis) {
      return (
        <>
          <img src={PortisIcon} alt={'portis logo'} />
          <MainWalletAction
            onClick={() => {
              portis.portis.showPortis()
            }}
          >
            Show Portis
          </MainWalletAction>
        </>
      )
    }
    return null
  }

  const clearAllTransactionsCallback = useCallback(() => {
    if (chainId) dispatch(clearAllTransactions({ chainId }))
  }, [dispatch, chainId])

  return (
    <>
      <UpperSection>
        <HeaderRow>Account</HeaderRow>
        <AccountSection>
          <Title>{formatConnectorName()}</Title>
          <Address id="web3-account-identifier-row">
            {ENSName ? (
              <>
                <div>{getStatusIcon()}</div>
                <p> {ENSName}</p>
              </>
            ) : (
              <>
                <div>{getStatusIcon()}</div>
                <p> {account && shortenAddress(account)}</p>
              </>
            )}
          </Address>
          <AccountGroupingRow>
            {ENSName ? (
              <>
                {account && (
                  <Copy toCopy={account}>
                    <span>Copy Address</span>
                  </Copy>
                )}
                {chainId && account && (
                  <AddressLink
                    hasENS={!!ENSName}
                    isENS={true}
                    href={chainId && getEtherscanLink(chainId, ENSName, 'address')}
                  >
                    <span>View on Etherscan</span>
                    <IconBtn>
                      <LinkIcon />
                    </IconBtn>
                  </AddressLink>
                )}
              </>
            ) : (
              <>
                {account && (
                  <Copy toCopy={account}>
                    <span>Copy Address</span>
                  </Copy>
                )}
                {chainId && account && (
                  <AddressLink hasENS={!!ENSName} isENS={false} href={getEtherscanLink(chainId, account, 'address')}>
                    <span>View on Etherscan</span>
                    <IconBtn>
                      <LinkIcon />
                    </IconBtn>
                  </AddressLink>
                )}
              </>
            )}
          </AccountGroupingRow>
        </AccountSection>
        <ButtonGroup>
          {connector !== injected && connector !== walletlink && (
            <ButtonWrapper
              type="primary"
              shadow
              onClick={() => {
                ;(connector as any).close()
              }}
            >
              Disconnect
            </ButtonWrapper>
          )}
          <ButtonWrapper
            type="primary"
            shadow
            onClick={() => {
              openOptions()
            }}
          >
            Change
          </ButtonWrapper>
        </ButtonGroup>
      </UpperSection>
      {!!pendingTransactions.length || !!confirmedTransactions.length ? (
        <LowerSection>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 10 }}>
            <div>Recent Transactions</div>
            <span style={{ cursor: 'pointer' }} onClick={clearAllTransactionsCallback}>
              clear all
            </span>
          </div>
          {renderTransactions(pendingTransactions)}
          {renderTransactions(confirmedTransactions)}
        </LowerSection>
      ) : (
        <LowerSection>Your transactions will appear here...</LowerSection>
      )}
    </>
  )
}
