import { Web3Provider } from '@ethersproject/providers'
import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { WalletLinkConnector } from '@web3-react/walletlink-connector'
import { PortisConnector } from '@web3-react/portis-connector'
import { ChainId } from '@oolongswap/oolongswap-sdk'

import { FortmaticConnector } from './Fortmatic'
import { NetworkConnector } from './NetworkConnector'
import OOLONGSWAP_LOGO_URL from '../assets/imgs/Logo.svg'

const INFURA_KEY = process.env.REACT_APP_INFURA_KEY
const FORMATIC_KEY = process.env.REACT_APP_FORTMATIC_KEY
const PORTIS_ID = process.env.REACT_APP_PORTIS_ID

if (typeof INFURA_KEY === 'undefined') {
  throw new Error(`REACT_APP_INFURA_KEY must be a defined environment variable`)
}

export const ALL_SUPPORTED_CHAIN_IDS: ChainId[] = [
  ChainId.BOBA,
  ChainId.BOBA_RINKEBY,
  ChainId.RINKEBY
]
// BOBA NOTE: update urls
const NETWORK_URLS: { [key in ChainId]: string } = {
  [ChainId.BOBA]: `https://boba.network`,
  [ChainId.BOBA_RINKEBY]: `https://rinkeby.boba.network`,
  [ChainId.RINKEBY]: `https://rinkeby.infura.io/v3/${INFURA_KEY}`
}

// BOBA NOTE: update defaultChainId
export const network = new NetworkConnector({
  urls: NETWORK_URLS,
  defaultChainId: ChainId.RINKEBY,
})

let networkLibrary: Web3Provider | undefined
export function getNetworkLibrary(): Web3Provider {
  return (networkLibrary = networkLibrary ?? new Web3Provider(network.provider as any))
}

export const injected = new InjectedConnector({
  supportedChainIds: ALL_SUPPORTED_CHAIN_IDS,
})

export const walletconnect = new WalletConnectConnector({
  supportedChainIds: ALL_SUPPORTED_CHAIN_IDS,
  rpc: NETWORK_URLS,
  qrcode: true,
})

// mainnet only
export const fortmatic = new FortmaticConnector({
  apiKey: FORMATIC_KEY ?? '',
  chainId: 1
})

// mainnet only
export const portis = new PortisConnector({
  dAppId: PORTIS_ID ?? '',
  networks: [1]
})

// mainnet only
export const walletlink = new WalletLinkConnector({
  url: NETWORK_URLS[ChainId.BOBA],
  appName: 'Oolongswap',
  appLogoUrl: OOLONGSWAP_LOGO_URL,
})