import React, { memo } from 'react'
import { Discord, MoreIcon, Telegram, Twitter } from 'assets/imgs'
import { useTranslation } from 'react-i18next'
import Button from 'templates/Button/Button'
import Dropdown from 'templates/Dropdown/Dropdown'
import styled from 'styled-components'

const OverlayWrapper = styled.ul`
  padding: 20px;
  width: 222px;
  display: grid;
  gap: 10px;
  grid-auto-flow: row;
`
const Block = styled.li`
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  color: ${({ theme }) => theme.font60};
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  :hover {
    color: ${({ theme }) => theme.bgc1};
  }
`

const More = memo(() => {
  const { t } = useTranslation()
  const LINKS = [
    { path: '1', text: t('Github'), icon: <Twitter /> },
    { path: '2', text: t('Documentation'), icon: <Twitter /> },
    { path: '3', text: t('Analysis'), icon: <Twitter /> },
    { path: '4', text: t('Twitter'), icon: <Twitter /> },
    { path: '5', text: t('Telegram'), icon: <Telegram /> },
    { path: '6', text: t('Discord'), icon: <Discord /> }
  ]

  const Overlay = (
    <OverlayWrapper>
      {LINKS.map(p => (
        <Block key={p.path}>
          <span>{p.text}</span>
          {p.icon}
        </Block>
      ))}
    </OverlayWrapper>
  )

  return (
    <Dropdown overlay={Overlay} trigger={['click']}>
      <Button type="primary" style={{ padding: '0 10px' }}>
        <MoreIcon style={{ margin: 0 }} />
      </Button>
    </Dropdown>
  )
})

export default More
