import styled from 'styled-components'
import React from 'react'
import { SpinIcon } from 'templates/Loading/Spin'

const Wrapper = styled.button`
  padding: 0 14px;
  height: 44px;
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  border-radius: 8px;
  border: 1px solid;
  border-color: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    margin-left: 4px;
  }

  &[data-type='primary'] {
    color: ${({ theme }) => theme.primary};
    background: ${({ theme }) => theme.bgc3};
    :hover,
    :active,
    :focus {
      border-color: ${({ theme }) => theme.primary};
    }
    &[data-shadow] {
      box-shadow: 8px 8px 0px ${({ theme }) => theme.primary};
      :active,
      :focus {
        box-shadow: none;
      }
    }
  }

  &[data-type='danger'] {
    color: ${({ theme }) => theme.bgc4};
    background: ${({ theme }) => theme.danger1};
    :hover,
    :active,
    :focus {
      border-color: ${({ theme }) => theme.danger2};
    }
  }

  &[disabled] {
    color: ${({ theme }) => theme.font30};
    background: ${({ theme }) => theme.font20};
    cursor: not-allowed;
    box-shadow: none !important;
    :hover,
    :active,
    :focus {
      border-color: transparent;
    }
  }
`

type TProps = Overwrite<
  React.ButtonHTMLAttributes<HTMLButtonElement>,
  {
    children?: React.ReactNode
    type: 'primary' | 'danger'
    shadow?: boolean
    loading?: boolean
  }
>

const Button: React.FC<TProps> = ({ children, type, shadow, loading, ...props }) => {
  return (
    <Wrapper type="button" data-type={type} data-shadow={shadow ? '' : undefined} {...props}>
      {children}
      {loading && <SpinIcon />}
    </Wrapper>
  )
}

export default Button
